<template>
	<view class="xinyi-content">
		<xinyi-nav-bar title="注册" leftIcon="undo-filled"></xinyi-nav-bar>
		<view class="register-form__view">
			<view class="register-top__view">
				<view class="register-title__view">新用户注册</view>
			</view>
			<uni-forms class="form" ref="regForm" :modelValue="user" label-width="80px" :rules="rules">
				<uni-forms-item label="姓名" required name="userName">
					<uni-easyinput
						v-model="user.userName"
						placeholder="请输入姓名"
						clearable
						trim
						primaryColor="#215e21"
					></uni-easyinput>
				</uni-forms-item>

				<uni-forms-item label="密码" required name="password">
					<uni-easyinput
						v-model="user.password"
						placeholder="请输入密码"
						type="password"
						clearable
						trim
						primaryColor="#215e21"
					></uni-easyinput>
				</uni-forms-item>

				<uni-forms-item label="确认密码" required name="confirmPassword">
					<uni-easyinput
						v-model="user.confirmPassword"
						placeholder="请确认密码"
						type="password"
						trim
						:errorMessage="user.password != user.confirmPassword"
						primaryColor="#215e21"
						@blur="checkConfirmPwd"
					></uni-easyinput>
				</uni-forms-item>

				<uni-forms-item label="手机号" required name="mobile">
					<uni-easyinput
						v-model="user.mobile"
						placeholder="请输入手机号"
						trim
						primaryColor="#215e21"
					></uni-easyinput>
				</uni-forms-item>

				<uni-forms-item label="昵称" required name="nickName">
					<uni-easyinput
						v-model="user.nickName"
						placeholder="请输入昵称"
						clearable
						trim
						primaryColor="#215e21"
					></uni-easyinput>
				</uni-forms-item>

				<uni-forms-item label="介绍" required name="institution">
					<uni-easyinput
						v-model="user.institution"
						placeholder="请输入介绍"
						clearable
						trim
						primaryColor="#215e21"
					></uni-easyinput>
				</uni-forms-item>
			</uni-forms>
			<button class="register__btn" hover-class="register-hover" type="primary" @click="register()">注 册</button>
		</view>
	</view>
</template>

<script>
import { register } from '@/api/user.js';
export default {
	data() {
		return {
			user: {
				permission: 'normal'
			},
			// 校验规则
			rules: {
				userName: {
					rules: [
						{
							required: true,
							errorMessage: '姓名不能为空'
						}
					]
				},
				password: {
					rules: [
						{
							required: true,
							errorMessage: '密码不能为空'
						}
					]
				},
				mobile: {
					rules: [
						{
							required: true,
							errorMessage: '手机号不能为空'
						}
					]
				},
				nickName: {
					rules: [
						{
							required: true,
							errorMessage: '昵称不能为空'
						}
					]
				},
				institution: {
					rules: [
						{
							required: true,
							errorMessage: '介绍不能为空'
						}
					]
				}
			}
		};
	},
	onReady() {
		// 需要在onReady中设置规则
		this.$refs.regForm.setRules(this.rules);
	},
	methods: {
		register() {
			this.$refs.regForm
				.validate()
				.then((res) => {
					register(this.user)
						.then((res) => {
							if (res.data.code == 200) {
								this.user = {};
								uni.showToast({
									title: '注册成功',
									icon: 'none',
									complete() {
										setTimeout(function () {
											uni.reLaunch({
												url: '/pages/login/login'
											});
										}, 1000);
									}
								});
							}
						})
						.catch((err) => {});
				})
				.catch((err) => {});
		},
		// 检查确认密码
		checkConfirmPwd() {
			if (this.user.password != this.user.confirmPassword) {
				uni.showToast({
					title: '两次输入密码不一致',
					icon: 'none'
				});
				return false;
			}
		}
	}
};
</script>

